<template>
<v-container fluid fill-height>
    <v-row align="center" justify="center">
        <v-col cols="12" sm="8" md="6">
            <v-card>
                <v-card-title>Password Reset Confirmation</v-card-title>
                <v-card-text>
                    <v-form ref="form" @submit.prevent="submit">
                        <v-text-field v-model="password" label="New Password" :rules="passwordRules" type="password" required></v-text-field>
                        <v-text-field v-model="passwordConfirm" label="Confirm New Password" :rules="passwordConfirmRules" type="password" required></v-text-field>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" type="submit">Reset Password</v-btn>
                        </v-card-actions>
                    </v-form>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</v-container>
</template>

  
<script>
import axios from "axios";

export default {
    data() {
        return {
            password: "",
            passwordConfirm: "",
            passwordRules: [
                (value) => !!value || "Password is required",
                (value) => value.length >= 8 || "Password must be at least 8 characters",
            ],
            passwordConfirmRules: [
                (value) => !!value || "Password confirmation is required",
                (value) =>
                value === this.password || "Password and confirmation must match",
            ],
        };
    },
    methods: {
        async submit() {
            if (this.$refs.form.validate()) {
                try {
                    // Extract uid and token from the route parameters
                    const uid = this.$route.params.uid;
                    const token = this.$route.params.token;

                    // Send the password reset request
                    await axios.post("/api/v1/users/reset_password_confirm/", {
                        uid,
                        token,
                        new_password: this.password,
                        re_new_password: this.passwordConfirm,
                    });

                    // Redirect the user to the login page after a successful password reset
                    this.$router.push('/login')
                } catch (error) {
                    console.error("An error occurred while resetting your password:", error);
                    alert("An error occurred while resetting your password. Please try again.");
                }
            }
        },
    },
};
</script>
