<template>
<v-container fluid class="edit-profile fill-height">
    <v-row align="center" justify="center" class="fill-height">
        <v-col cols="12" sm="8" md="6">
            <h1>Edit Profile</h1>
            <v-form @submit.prevent="submitForm">
                <v-text-field label="First Name" v-model="formData.first_name" :rules="[rules.required]" required></v-text-field>
                <v-text-field label="Last Name" v-model="formData.last_name" :rules="[rules.required]" required></v-text-field>
                <v-text-field label="Username" v-model="formData.username" :rules="[rules.required]" required></v-text-field>
                <v-text-field label="Email" v-model="formData.email" :rules="[rules.required, rules.email]" required type="email"></v-text-field>
                <v-btn type="submit" color="primary">Save Changes</v-btn>
            </v-form>
            <v-snackbar v-model="snackbar.show" :color="snackbar.color" :timeout="5000" top>
                {{ snackbar.text }}
                <v-btn color="white" text @click="snackbar.show = false">Close</v-btn>
            </v-snackbar>
        </v-col>
    </v-row>
</v-container>
</template>

<script>
import axios from 'axios';

export default {
    beforeCreate() {
        this.$store.commit('initializeStore')
        const token = this.$store.state.token
        console.log(this.$store.state.isAuthenticated)

        if (token) {
            axios.defaults.headers.common['Authorization'] = "Token " + token
        } else {
            axios.defaults.headers.common['Authorization'] = ''
        }
        axios
            .get('/api/v1/users/me/', token)
            .then(response => {
                this.userInfo = response.data
                console.log(this.userInfo)
                this.formData.username = this.userInfo.username
                this.formData.email = this.userInfo.email
                this.formData.first_name = this.userInfo.first_name
                this.formData.last_name = this.userInfo.last_name

            })
            .catch(error => {
                console.log(error)
            })
    },
    data() {
        return {
            formData: {
                first_name: '',
                last_name: '',
                username: '',
                email: ''
            },
            rules: {
                required: value => !!value || 'Required.',
                email: value =>
                    /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'Invalid email.'
            },
            snackbar: {
                show: false,
                text: '',
                color: 'success'
            }

        };

    },
    methods: {
        async submitForm() {
            try {
                await axios.post('/api/edit_profile/' + this.userInfo.username + '/', this.formData);
                window.scrollTo(0, 0); // Scroll to top
                this.snackbar.text = 'Profile updated successfully';
                this.snackbar.color = 'success';
                this.snackbar.show = true;
            } catch (error) {
                console.error(error)
                window.scrollTo(0, 0); // Scroll to top
                this.snackbar.text = 'Failed to update profile';
                this.snackbar.color = 'error';
                this.snackbar.show = true;;
            }
        }
    }
};
</script>
