<script>
import axios from 'axios'

export default {
    computed: {
        hasUnreadNotifications() {
            return this.notifications.some((notification) => !notification.is_read);
        },
    },
    name: 'App',
    beforeCreate() {
        this.$store.commit('initializeStore')
        const token = this.$store.state.token //fetches token in local storage
        console.log(this.$store.state.isAuthenticated)

        if (token) {
            axios.defaults.headers.common['Authorization'] = "Token " + token 
        } else {
            axios.defaults.headers.common['Authorization'] = ''
        }
        axios
            .get('/api/v1/users/me/', token) //get request for user data of the token supplied
            .then(response => {
                this.userInfo = response.data //store user data
                console.log(this.userInfo)
                this.fetchNotifications();

            })
            .catch(error => {
                console.log(error)
            })
    },
    methods: {
        signOut() {
            axios
                .post('/api/v1/token/logout/', this.$store.state.token) // log out request
                .then(response => {
                    this.$store.commit('removeToken'); // removes token from local storage
                    console.log(response);
                    window.location.reload();
                })
                .catch(error => {
                    console.log(error);
                })

        },
        async fetchNotifications() {
            try {
                const response = await axios.get("/notifications/" + this.userInfo.username + "/");
                this.notifications = response.data.notifications;
            } catch (error) {
                console.error("Error fetching notifications:", error);
            }
        },
        dismissNotification(notificationId) {
            axios
                .post(`/api/mark_notification_as_read/${notificationId}/`)
                .then((response) => {
                    if (response.data.success) {
                        this.notifications = this.notifications.filter(
                            (notification) => notification.id !== notificationId
                        );
                    } else {
                        console.error('Failed to dismiss notification.');
                    }
                })
                .catch((error) => {
                    console.error('Failed to dismiss notification:', error);
                });
        },
    },

    data() {
        return {
            userInfo: '',
            drawer: false,
            profileDialog: false,
            notifications: [],
        };
        // return {
        //   menus: [
        //     { title: "About us", route: 'about' },

        //   ]
        // }

    }
}
</script>

