<template>
    <v-app>
      <div class="background"></div>
      <v-main class="d-flex justify-center align-center">
        <v-col cols="10" lg="4" class="mx-auto">
          <v-card class="pa-4" color="grey-darken-4">
            <div class="text-center">
              <v-avatar size="100" color="grey">
                <v-icon size="40" color="white">mdi-account</v-icon>
              </v-avatar>
               <h1 class="white--text">Create an account</h1>
  
            </div>
            <v-form @submit.prevent="signUpForm" ref="form">
              <v-card-text>
              <v-text-field
              v-model="email" 
                :rules="emailRules"
                color="white"
                type="email"
                label="Email"
                placeholder="Email">
              </v-text-field>
              <v-text-field
              v-model="firstName" 
                color="white"
                type="text"
                label="First name"
                placeholder="First Name">
              </v-text-field>
              <v-text-field
              v-model="lastName" 
                color="white"
                type="text"
                label="Last name"
                placeholder="Last Name">
              </v-text-field>
              <v-text-field
              v-model="username" 
               :rules="usernameRules"
                color="white"
                type="text"
                label="Username"
                placeholder="Username">
              </v-text-field>
              <v-text-field
                v-model="password"
                :rules="passwordRules"
                :type="showPassword ? 'text' : 'password'"
                color="white"
                label="Password"
                placeholder="Password"
                :append-icon="showPassword ? 'mdi-eye':'mdi-eye-off'"
                @click:append="showPassword = !showPassword">
              </v-text-field>
            </v-card-text>
            
            <router-link class="link" to="/login">Already have an account? Sign in</router-link>
            <v-card-actions class="justify-center">
              <v-btn :disabled="!isFormFilled" :loading="loadingAnim" type="submit" color="indigo">
                <span class="white--text px-8">Continue</span> 
              </v-btn>
            </v-card-actions>
            </v-form>
          </v-card>
        </v-col>
      </v-main>

      <v-snackbar color="red" v-model="snackbar">
        Sign up Failed. Please try again
      </v-snackbar>
      
    </v-app>
  </template>
  
  <script>
  import axios from 'axios'
  
  export default {
    computed: {
    isFormFilled() {
      return (
        this.email &&
        this.firstName &&
        this.lastName &&
        this.username &&
        this.password
      );
    },
  },
    name: 'SignUp',
  
    components: {
    },
  
    data: () => ({
      snackbar:false,

      firstName:'',
      lastName:'',


      showPassword:false,
      password: '',
      passwordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >= 7) || 'Password must be less than 10 characters',
        v => /.*[!@#$&*]/.test(v) || 'Password must have one special character',
        v => /.*[A-Z].*[A-Z]/.test(v) || 'Password must have two uppercase letters',
      ],
      email: '',
      emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+/.test(v) || 'E-mail must be valid',
        ],

      username:'',
      usernameRules:[
        v=> !!v || 'Username is required',

        ],
      loadingAnim:false,
  
      //
    }),
    methods:{
      signUpForm(){
        if(this.$refs.form.validate()){ //checks to see if inputs are valid
          this.loadingAnim=true 

          const formData ={ //form data that will be posted
            email: this.email,
            username: this.username,
            password: this.password,
            first_name: this.firstName, //custom field
            last_name: this.lastName, //custom field
          }

          axios
            .post('/api/v1/users/', formData) //posts the formData to add user using Djoser
            .then(response=>{
              this.$router.push('/login') //redirect to login page
              console.log(response)
            })
            .catch(error =>{
              console.log(error)
            })
            setTimeout(() => {
              this.loadingAnim=false, //display error if server fails
              this.snackbar=true

            },3000)
        }
      },
    }
  };
  </script>
  <style scoped>
  .background{
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url("https://images.pexels.com/photos/690779/pexels-photo-690779.jpeg") !important;
    height:100%;
    width:100%;
    display: block;
    position: absolute;
    top:0;
    background-size: cover;
  
  }
  .link {
  color: #1976D2; /* Replace with your desired primary color */
  text-decoration: none;
}

.link:hover, .link:active, .link:focus {
  color: #1976D2; /* Replace with your desired primary color */
  text-decoration: none;
}
  </style>
  