<template>
<v-app id="inspire">

    <v-main class="bg-grey-lighten-3" dark>
        <v-container fluid class="dashboard">

            <v-row>
                <v-col cols="2" class="pt-16">
                    <v-sheet rounded="lg">
                        <v-card-title>
                            <v-subheader align="center">
                                In progress
                            </v-subheader>
                        </v-card-title>
                        <v-list rounded="lg">
                            <v-list-item v-for="n in this.ongoingProjects.length" :key="n" link @click="this.selectedProject = this.ongoingProjects[n - 1] ; this.addPage = false; this.projectPage = true; getProjectSongs(); fetchLogs();">
                                <v-list-item-title>
                                    {{ this.ongoingProjects[n - 1].name }}
                                </v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-sheet>
                    <v-sheet rounded="lg" class="mt-4">
                        <v-card-title>
                            <v-subheader align="center">
                                Completed
                            </v-subheader>
                        </v-card-title>
                        <v-list rounded="lg">
                            <v-list-item v-for="n in this.completedProjects.length" :key="n" link @click="this.selectedProject = this.completedProjects[n - 1] ; this.addPage = false; this.projectPage = true; getProjectSongs(); fetchLogs(); ">
                                <v-list-item-title>
                                    {{ this.completedProjects[n - 1].name }}
                                </v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-sheet>
                    <v-row justify="center">
                        <v-btn rounded color="white" @click="projectDialog = true" class="mt-7">
                            <v-icon left>mdi-plus</v-icon>
                            Add Project
                        </v-btn>
                    </v-row>
                </v-col>

                <v-col class="pt-16">
                    <v-sheet min-height="70vh" rounded="lg">
                        <v-card :color="albumColor[2]" theme="dark">
                            <div class="project_info" v-if="this.projectPage == true">
                                <div class="d-flex flex-no-wrap justify-space-between">
                                    <div>
                                        <v-card-subtitle class="text-h6 font-weight-light mb-1 pt-3">Album</v-card-subtitle>
                                        <v-card-title class="text-h3 font-weight-bold mb-3 " style="font-family: 'Helvetica Neue', sans-serif; line-height: 1;">
                                            {{ this.selectedProject.name }}
                                            <!-- Option to add collaborator is available if the user is an admin -->
                                            <v-btn class="transparent"  icon @click="addCollaboratorDialog = true" v-if="this.userInfo.username === this.selectedProject.creator">
        <v-icon>mdi-account-plus</v-icon>
    </v-btn>

    <v-dialog theme ="light" v-model="addCollaboratorDialog" max-width="600">
        <v-card>
            <v-card-title class="headline">Add Collaborator</v-card-title>
            <v-card-text>
                <v-form ref="addCollaboratorForm" v-model="valid" lazy-validation>
                    <v-text-field
                        v-model="collaboratorEmail"
                        :rules="emailRules"
                        label="Email"
                        required
                    ></v-text-field>
                </v-form>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="addCollaboratorDialog = false">Close</v-btn>
                <v-btn color="blue darken-1" text @click="submitCollaboratorEmail">Add</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

                                            <v-menu offset-y v-if="$store.state.isAuthenticated" theme="light">
                                                <template v-slot:activator="{ props }">
                                                    <v-btn icon v-bind="props" class="transparent">
                                                        <v-icon>mdi-dots-vertical</v-icon>
                                                    </v-btn>
                                                </template>
                                                <v-card>
                                                    <v-list>
                                                        <!-- The button to edit the project appears if the user is an admin -->
                                                        <v-list-item @click="editDialog = true" v-if="this.userInfo.username === this.selectedProject.creator">
                                                            <v-list-item-title>Edit</v-list-item-title>
                                                        </v-list-item>
                                                        <!-- Mark project as complete button appears if the user is an admin -->
                                                        <v-list-item v-if="this.userInfo.username === this.selectedProject.creator && this.selectedProject.completed === false" @click="markComplete(this.selectedProject.name)">
                                                            <v-list-item-title>Mark as complete</v-list-item-title>
                                                        </v-list-item>
                                                        <v-list-item v-if="this.userInfo.username === this.selectedProject.creator && this.selectedProject.completed === true" @click="markComplete(this.selectedProject.name)">
                                                            <v-list-item-title>Mark as incomplete</v-list-item-title>
                                                        </v-list-item>
                                                        <!-- If the user is an admin, the 'Delete Project' button will appear -->
                                                        <v-list-item v-if="this.userInfo.username === this.selectedProject.creator" @click="deleteProject(this.selectedProject.name)">
                                                            <v-list-item-title>Delete</v-list-item-title>
                                                        </v-list-item>
                                                        <v-list-item v-if="this.userInfo.username !== this.selectedProject.creator" @click="leaveProject(this.selectedProject.name, this.userInfo.username)">
                                                            <v-list-item-title>Leave Project</v-list-item-title>
                                                        </v-list-item>
                                                    </v-list>
                                                </v-card>
                                            </v-menu>

                                        </v-card-title>

                                        <v-dialog v-model="editDialog" max-width="600px">
                                            <v-card theme="light">
                                                <img :src="'http://192.168.0.93:8000/' + this.selectedProject.coverart" class="coverImage" />
                                                <v-card-title>
                                                    <div style="text-align: center;">
                                                        <span class="headline">Edit Project</span>
                                                    </div>
                                                </v-card-title>

                                                <v-card-text>
                                                    <v-container>
                                                        <v-row>
                                                            <v-col cols="12">
                                                                <v-file-input :rules="rules" v-model="image" accept="image/png, image/jpeg, image/bmp" placeholder="Choose your cover art" label="Cover art" @change="onFileInput($event)" required>
                                                                </v-file-input>
                                                            </v-col>

                                                            <v-col cols="12">
                                                                <v-text-field v-model="this.selectedProject.name" :counter="10" label="Project Name" placeholder="Enter project name"></v-text-field>
                                                            </v-col>

                                                            <v-col cols="12">
                                                                <v-select v-model="this.selectedProject.DAW" :items="items" :rules="[v => !!v || 'Item is required']" label="DAW" placeholder="Select DAW">
                                                                </v-select>
                                                            </v-col>
                                                            <v-col cols="12">
                                                                <VueDatePicker v-model="this.selectedProject.deadline" placeholder="Select deadline"></VueDatePicker>
                                                            </v-col>
                                                        </v-row>
                                                    </v-container>
                                                </v-card-text>

                                                <v-card-actions>
                                                    <v-spacer></v-spacer>
                                                    <v-btn color="blue darken-1" text @click="editDialog = false">Cancel</v-btn>
                                                    <v-btn color="blue darken-1" text @click="editsubmitForm">Submit</v-btn>
                                                </v-card-actions>
                                            </v-card>
                                        </v-dialog>

                                        <v-card-subtitle class="mb-3" style="text-transform: capitalize;">
                                            Collaborators: <span v-for="(collaborator, index) in this.selectedProject.Collaborators" :key="index">{{ collaborator }}{{ index < this.selectedProject.Collaborators.length - 1 ? ', ' : '' }}</span>
                                        </v-card-subtitle>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon color="white">mdi-check</v-icon> Completed: {{ this.selectedProject.completed }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon left>mdi-calendar</v-icon> Deadline: {{ formatDate(this.selectedProject.deadline).slice(0, 10) }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon class="mr-1">mdi-calendar-clock</v-icon>
                                            Created: {{ formatDate(selectedProject.created_at) }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon small class="mr-1">mdi-calendar-edit</v-icon>
                                            Updated: {{ formatDate(selectedProject.updated_at) }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon left>mdi-monitor</v-icon>
                                            DAW: {{ this.selectedProject.DAW }}
                                        </v-chip>
                                    </div>

                                    <v-card class="pa-0" elevation="2" style="width: 200px; height: 200px; overflow: hidden; display: flex; justify-content: center; align-items: center;">
                                        <v-img :src="'http://192.168.0.93:8000/' + this.selectedProject.coverart" style="object-fit: cover; width: 100%; height: 100%;"></v-img>
                                    </v-card>

                                </div>
                            </div>
                        </v-card>

                        <div class="d-flex align-center flex-column">
                            <v-col cols="12" sm="6" md="4" v-if="this.projectPage == true">
                                <div>

                                    <v-dialog v-model="dialog" max-width="600px">
                                        <v-card>
                                            <v-card-title>
                                                <div style="text-align: center;">
                                                    <span class="headline">Add Song</span>
                                                </div>
                                            </v-card-title>

                                            <v-card-text>
                                                <v-container>
                                                    <v-row>
                                                        <v-col cols="12">
                                                            <v-text-field label="Title" v-model="song.title"></v-text-field>
                                                        </v-col>

                                                        <v-col cols="12">
                                                            <v-textarea label="Description" v-model="song.description"></v-textarea>
                                                        </v-col>

                                                        <v-col cols="12">
                                                            <v-file-input label="Upload audio file" @change="onFileSelected" accept="audio/*"></v-file-input>
                                                        </v-col>
                                                    </v-row>
                                                </v-container>
                                            </v-card-text>

                                            <v-card-actions>
                                                <v-spacer></v-spacer>
                                                <v-btn color="blue darken-1" text @click="dialog = false">Cancel</v-btn>
                                                <v-btn color="blue darken-1" text @click="submitSongForm">Submit</v-btn>
                                            </v-card-actions>
                                        </v-card>
                                    </v-dialog>
                                </div>
                            </v-col>
                        </div>
                        <div v-if="this.projectPage == true">
                            <!-- this is a centred table that will contain information about songs in a project -->
                            <v-container class="mt-5">
                                <v-row>
                                    <v-col md="8">
                                        <v-card rounded>
                                            <v-card-text>
                                                <v-table>
                                                    <thead>
                                                        <tr>
                                                            <th class="text-left">
                                                                Title
                                                            </th>
                                                            <th class="text-left">
                                                                Completed
                                                            </th>
                                                            <th class="text-left">
                                                                Created at
                                                            </th>
                                                            <th class="text-left">
                                                                Updated at
                                                            </th>
                                                            <th class="text-right">
                                                                Action
                                                            </th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-for="item in this.selectedProjectSongs.Projects" :key="item.name" :class="{ 'table-row-hover': true }" @click="onRowClick(item)">
                                                            <td class="capitalize">{{ item.name }}</td>
                                                            <td class="capitalize">{{ item.completed }}</td>
                                                            <td>{{ formatDate(item.created_at) }}</td>
                                                            <td>{{ formatDate(item.updated_at) }}</td>
                                                            <td class="text-right">
                                                                <v-dialog v-model="confirmDelete" max-width="500px">
                                                                    <v-card>
                                                                        <v-card-title>
                                                                            Confirm Delete
                                                                        </v-card-title>
                                                                        <v-card-text>
                                                                            Are you sure you want to delete this song?
                                                                        </v-card-text>
                                                                        <v-card-actions>
                                                                            <v-btn text @click="confirmDelete = false">
                                                                                Cancel
                                                                            </v-btn>
                                                                            <v-btn color="error" @click="deleteItem(item.name); confirmDelete = false;">
                                                                                Delete
                                                                            </v-btn>
                                                                        </v-card-actions>
                                                                    </v-card>
                                                                </v-dialog>
                                                                <!-- The delete song button appears if the user is an admin -->
                                                                <v-icon  v-if="this.userInfo.username === this.selectedProject.creator" @click.stop="confirmDelete = true">mdi-delete</v-icon>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </v-table>
                                            </v-card-text>
                                            <div style="display: flex; justify-content: center; padding-bottom: 10px;">
                                                <!-- If the user is an admin, the 'Add Song' button will appear -->
                                                <v-btn rounded :color="white" @click="dialog = true" v-if="this.userInfo.username === this.selectedProject.creator">
                                                    <v-icon left>mdi-plus</v-icon>
                                                    Add Song
                                                </v-btn>
                                            </div>

                                        </v-card>

                                    </v-col>

                                    <!-- This is the card that will have tiny windows for logs, messages and tasks that will be next to the table of songs -->

                                    <v-col md="4">
                                        <v-card>
                                            <v-tabs v-model="tab" :color=this.albumColor[2] class="mt-3 centered-tab-buttons" grow>
                                                <v-tab value="one">Tasks</v-tab>
                                                <v-tab value="two">Messages</v-tab>
                                                <v-tab value="three">Logs</v-tab>
                                            </v-tabs>
                                            <v-card-text>
                                                <v-window v-model="tab">
                                                    <v-window-item value="one">
                                                        <v-card v-for="(task, index) in this?.tasks" :key="index" width="341" :title="task.title" 
                                                        :subtitle="task.song" :text="task.description" style="">

                                                            <v-card-actions>

                                                                <v-btn rounded v-if="task.completed===false" :color="white" @click="finishTaskDialog = true; selectedTask=task">
                                                                    <v-icon left>mdi-check</v-icon>
                                                                    Mark as complete
                                                                </v-btn>
                                                                <v-btn rounded :color="white" v-if="task.completed===true">
                                                                    <v-icon left>mdi-check</v-icon>
                                                                    Completed
                                                                </v-btn>
                                                            </v-card-actions>
                                                        </v-card>
                                                        <v-dialog v-model="finishTaskDialog" max-width="600px">
                                                            <v-card>
                                                                <v-card-title>
                                                                    <div style="text-align: center;">
                                                                        <span class="headline">Finish Task</span>
                                                                    </div>
                                                                </v-card-title>

                                                                <v-card-text>
                                                                    <v-container>
                                                                        <v-row>
                                                                            <v-col cols="12">
                                                                                <v-file-input v-if="this.typeSelect!=='DAW Files'" label="Upload file(s)" @change="onFileSelected" multiple></v-file-input>
                                                                                <v-file-input v-if="this.typeSelect==='DAW Files'" label="Upload DAW files" @change="ZipFiles" multiple></v-file-input>

                                                                            </v-col>
                                                                            <v-col cols="12">
                                                                                <v-textarea v-model="taskDescription" :counter="100" label="Description" required></v-textarea>
                                                                            </v-col>
                                                                            <v-col cols="12">
                                                                                <v-select v-model="typeSelect" @click="console.log(this.typeSelect)" :items="this.finishSelect" 
                                                                                :rules="[v => !!v || 'Item is required']" label="File type:" required></v-select>
                                                                            </v-col>

                                                                        </v-row>
                                                                    </v-container>
                                                                </v-card-text>

                                                                <v-card-actions>
                                                                    <v-spacer></v-spacer>
                                                                    <v-btn color="blue darken-1" text @click="finishTaskDialog = false">Cancel</v-btn>
                                                                    <v-btn color="blue darken-1" text @click="finishTaskSubmitForm()">Submit</v-btn>
                                                                </v-card-actions>
                                                            </v-card>
                                                        </v-dialog>

                                                        <div style="display: flex; justify-content: center; padding-bottom: 1px; padding-top: 20px;">
                                                            <!-- Option to add task becomes available if the user is an admin -->
                                                            <v-btn rounded :color="white" @click="taskDialog = true" v-if="this.userInfo.username === this.selectedProject.creator">
                                                                <v-icon left>mdi-plus</v-icon>
                                                                Add Task
                                                            </v-btn>
                                                        </div>
                                                    </v-window-item>
                                                    <v-window-item value="two">
                                                        <div v-for="message in messages" :key="message.timestamp">
                                                            <span class="mdi mdi-account-circle profile-icon"></span>
                                                            <span class="username">{{ message.user }}:</span>
                                                            <span class="message">{{ message.content }}</span>
                                                        </div>
                                                        <div class="message-input">
                                                            <input type="text" v-model="newMessage" placeholder="Type a message..." />
                                                            <button @click="sendMessage">Send</button>
                                                        </div>
                                                    </v-window-item>
                                                    <v-window-item value="three">
                                                        <div v-for="log in logs" :key="log.timestamp">
                                                            <span class="user-  name">{{ log.user }}:</span>
                                                            <span class="action">{{ log.action }}</span>
                                                            <span class="model">{{ log.model_name }}</span>
                                                            <span class="timestamp">at {{ new Date(log.timestamp).toLocaleString() }}</span>
                                                        </div>
                                                    </v-window-item>
                                                </v-window>
                                            </v-card-text>
                                        </v-card>
                                    </v-col>
                                </v-row>
                            </v-container>

                        </div>

                        <!-- This dialogue is for adding tasks -->

                        <div>
                            <v-dialog v-model="taskDialog" max-width="600px">
                                <v-card>
                                    <v-card-title>
                                        <div style="text-align: center;">
                                            <span class="headline">Add Task</span>
                                        </div>
                                    </v-card-title>

                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12">
                                                    <v-text-field v-model="taskTitle" :counter="50" label="Title" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-textarea v-model="taskDescription" :counter="100" label="Description" required></v-textarea>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select v-model="taskSongSelect" :items="this.songNames" :rules="[v => !!v || 'Item is required']" label="Song:" required></v-select>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select v-model="taskUserSelect" :items="this.selectedProject.Collaborators" :rules="[v => !!v || 'Item is required']" label="Assign to:" required></v-select>
                                                </v-col>

                                            </v-row>
                                        </v-container>
                                    </v-card-text>

                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="taskDialog = false">Cancel</v-btn>
                                        <v-btn color="blue darken-1" text @click="taskSubmitForm()">Submit</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>

                            <v-dialog v-model="projectDialog" max-width="600px">
                                <v-card>
                                    <img :src="this.imagePath" class="coverImage" />
                                    <v-card-title>
                                        <div style="text-align: center;">
                                            <span class="headline">Add Project</span>
                                        </div>
                                    </v-card-title>

                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12">
                                                    <v-file-input :rules="rules" v-model="image" accept="image/png, image/jpeg, image/bmp" placeholder="Choose your cover art" label="Cover art" @change="onFileInput($event)" required></v-file-input>
                                                </v-col>

                                                <v-col cols="12">
                                                    <v-text-field v-model="projectName" :counter="10" label="Project Name" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select v-model="select" :items="items" :rules="[v => !!v || 'Item is required']" label="DAW" required></v-select>
                                                </v-col>

                                                <v-col cols="12">
                                                    <v-text-field v-model="collabEmail" type="text" label="collaborators (separate multiple emails with a comma)" @blur="parseEmails"></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <VueDatePicker v-model="selectedDate" placeholder="Deadline"></VueDatePicker>
                                                </v-col>

                                            </v-row>
                                        </v-container>
                                    </v-card-text>

                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="projectDialog = false">Cancel</v-btn>
                                        <v-btn color="blue darken-1" text @click="projectSubmitForm">Submit</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </div>

                    </v-sheet>
                </v-col>
            </v-row>
        </v-container>
    </v-main>
</v-app>
</template>

<script>
import {
    ref
} from 'vue';
import ColorThief from 'colorthief';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import JSZip from "jszip";

import axios from 'axios'
export default {
    components: {
        VueDatePicker
    },

    beforeCreate() {
        this.projectPage = true
        console.log(this.current_page)

        this.$store.commit('initializeStore')
        const token = this.$store.state.token
        console.log(this.$store.state.isAuthenticated)

        if (token) {
            axios.defaults.headers.common['Authorization'] = "Token " + token
        } else {
            axios.defaults.headers.common['Authorization'] = ''
            this.$router.push('/login');

        }
        axios
            .get('/api/v1/users/me/', token)
            .then(response => {
                this.userInfo = response.data
                console.log(this.userInfo)
                this.getProjects()

            })
            .catch(error => {
                console.log(error)
            })

    },
    data: () => ({

        userInfo: '',
        projectInfo: '',
        selectedProject: '',
        selectedProjectSongs: '',
        projectLength: 0,

        dialog: false,
        projectDialog: false,
        editDialog: false,
        song: {
            title: '',
            description: '',
            audioFile: null,
        },

        selectedDate: null,
        addPage: false,
        projectPage: false,
        links: [
            'My Projects',
            'Messages',
            'Profile',
            'Updates',
        ],

        select: '',
        collabEmail: '',
        emailList: [],
        projectName: '',
        items: [
            'FL Studio',
            'Ableton Live',
            'Logic Pro',
            'Reason',
            'Avid Pro Tools'
        ],
        finishSelect: [
            'DAW Files',
            'Music File',
            'Stems'
        ],
        image: null,
        imagePath: '',
        confirmDelete: false,
        albumColor: '',
        completedProjects: [],
        ongoingProjects: [],

        confirmDelete: false,
        infoDialog: false,
        selectedItem: {},
        tab: null,

        // This is data for tasks
        finishTaskDialog: false,
        taskDialog: false,
        taskTitle: '',
        taskDescription: '',
        taskUserSelect: '',
        taskSongSelect: '',
        songNames: [],
        tasks: null,
        typeSelect: '',
        selectedTask: null,
        zippedFile: null,

        //data for messages
        messages: [],
        newMessage: "",

        //data for log data
        logs: [],


        //data for adding collaborators
        addCollaboratorDialog: false,
        collaboratorEmail: "",
        valid: false,
        emailRules: [
            v => !!v || "E-mail is required",
            v => /.+@.+\..+/.test(v) || "E-mail must be valid",
        ],

    }),

    methods: {
        onRowClick(item) {
            this.selectedItem = item;
            this.$router.push({
                name: 'SongView',
                params: {
                    songName: item.name,
                    projectName: this.selectedProject.name
                }
            });
        },
        //formats the date into a readable format 
        formatDate(dateString) {
            const date = new Date(dateString);
            const year = date.getFullYear();
            const month = (date.getMonth() + 1).toString().padStart(2, '0');
            const day = date.getDate().toString().padStart(2, '0');
            const hours = date.getHours().toString().padStart(2, '0');
            const minutes = date.getMinutes().toString().padStart(2, '0');

            return `${day}-${month}-${year} ${hours}:${minutes}`;
        },
        getProjects() {
            axios
                .get('/getProjects/' + this.userInfo.username + "/") //project data is fetched 
                .then(response => {

                    this.projectInfo = response.data //JSON stored
                    this.projectLength = this.projectInfo.Projects.length
                    if (this.projectInfo && this.projectInfo.Projects && this.projectInfo.Projects.length > 0) {
                        this.projectPage = true
                        this.selectedProject = this.projectInfo.Projects[0];
                    }
                    this.filterProjects(true) // filters to only complete projects
                    this.filterProjects(false) // filters to only projects incomplete projects
                    this.getProjectSongs();
                    this.fetchLogs();

                })
                .catch(error => {
                    console.log(error)

                })
        },
        getProjectSongs() {
            axios
                .get('/getProjectSongs/' + this.selectedProject.name + "/")
                .then(response => {
                    this.getDominantColor()

                    this.selectedProjectSongs = response.data
                    // this is to put each song name in an array for tasks.

                    this.songNames = [];
                    for (let i = 0; i < this.selectedProjectSongs.Projects.length; i++) {
                        this.songNames.push(this.selectedProjectSongs.Projects[i].name);
                    }
                    this.getTasks();
                    this.fetchMessages();
                    console.log(this.tasks)

                })
                .catch(error => {
                    console.log(error)

                })
        },
        //Submits the data via post of new project
        projectSubmitForm() {
            const formData = new FormData();
            formData.append('image', this.image[0]) // album cover image object
            formData.append('name', this.projectName)
            formData.append('deadline', this.selectedDate)
            formData.append('collabEmail', this.emailList)
            formData.append('DAW', this.select)

            console.log(this.emailList)
            axios
                .post('/getProjects/' + this.userInfo.username + "/", formData)
                .then(response => {
                    console.log(response)
                    this.getProjects() //refresh project list on selector 
                    this.image = null; //resets the image input field

                })
                .catch(error => {
                    console.log(error)

                })
            this.projectDialog = false; //the 'Add Project' window closes
        },
        //generates a url for the uploaded picture to display
        onFileInput(event) {
            const data = URL.createObjectURL(event.target.files[0]);  //Convert object into URL
            console.log(this.image[0])
            this.imagePath = data; //Create new variable with URL data
        },

        onFileSelected(event) {
            this.song.audioFile = event.target.files[0];
        },
        //Sends the song data to django via POST 
        submitSongForm() {
            console.log(this.song);
            // Submit the form and handle the response here
            const formData = new FormData();
            formData.append('file', this.song.audioFile)
            formData.append('name', this.song.title)
            formData.append('description', this.song.description)
            formData.append('username', this.userInfo.username)

            console.log(formData)
            axios
                .post('/getProjectSongs/' + this.selectedProject.name + "/", formData)
                .then(response => {
                    console.log(response)
                    this.getProjectSongs()
                    this.fetchLogs();

                })
                .catch(error => {
                    console.log(error)

                })
            this.dialog = false;
        },
        taskSubmitForm() {
            console.log(this.song);
            const formData = new FormData(); //input fields 
            formData.append('songName', this.taskSongSelect)
            formData.append('title', this.taskTitle)
            formData.append('description', this.taskDescription)
            formData.append('username', this.userInfo.username) //currently logged in admin user
            formData.append('workerUsername', this.taskUserSelect) // selected user from list of collaborators

            console.log(formData)
            axios
                .post('/getTasks/' + this.selectedProject.name + "/", formData)
                .then(response => {
                    console.log(response)
                    this.getTasks() //refreshes task list
                    this.fetchLogs(); //logs are added

                })
                .catch(error => {
                    console.log(error)

                })
            this.taskDialog = false; // task add window closes
        },
        finishTaskSubmitForm() {
            const formData = new FormData();
            formData.append('description', this.taskDescription)
            // there is a path for each potential file type with different axios requests
            if (this.typeSelect === 'Music File') { //if file type is a music file
                formData.append('file', this.song.audioFile);
                formData.append('username', this.userInfo.username);

                axios
                    .post('/getVersions/' + this.selectedTask.song + '/' + this.selectedProject.name + '/', formData)
                    .then(response => {
                        console.log(response);
                    })
                    .catch(error => {
                        console.log(error);
                    });
            }
            if (this.typeSelect === 'Stems') { //if file type is a stem
                formData.append('file', this.song.audioFile);
                formData.append('username', this.userInfo.username);

                axios
                    .post('/getStems/' + this.selectedTask.song + '/' + this.selectedProject.name + '/', formData)
                    .then(response => {
                        console.log(response);
                    })
                    .catch(error => {
                        console.log(error);
                    });
            }
            if (this.typeSelect === 'DAW Files') { //if file type is a DAW File
                formData.append('file', this.zippedFile); // sends the zip file instead of an audio file
                formData.append('username', this.userInfo.username);

                axios
                    .post('/getDAWFiles/' + this.selectedTask.song + '/' + this.selectedProject.name + '/', formData)
                    .then(response => {
                        console.log(response);
                    })
                    .catch(error => {
                        console.log(error);
                    });
            }
            axios
            //mark task as finished
                .post('/finishTask/' + this.selectedTask.id + "/") 
                .then(response => {
                    console.log(response)
                    this.getTasks() //fetch latest task data
                    this.fetchLogs();

                })
                .catch(error => {
                    console.log(error)

                })
            this.finishTaskDialog = false; //window closes
        },
        getTasks() {
            axios
                .get('/getTasks/' + this.selectedProject.name + "/") //access getTasks api call
                .then(response => {
                    this.tasks = response.data 
                    this.tasks = this.filteredTasks(); //filters display tasks that are currently assigned to the user
                    console.log(this.tasks)
                })
                .catch(error => {
                    console.log(error)

                })

        },
        //deletes song entries in project
        deleteItem(item) {
            axios.defaults.xsrfHeaderName = "X-CSRFToken";
            axios.defaults.xsrfCookieName = "csrftoken";
            axios.delete(`/deleteSong/${item}/`, {
                    withCredentials: true
                })
                .then(response => {

                    console.log(`Item with ID ${item} deleted successfully!`);
                    this.getProjectSongs()
                    this.fetchLogs();
                })
                .catch(error => {

                    console.log(`Error deleting item with ID ${item}: ${error.message}`);
                });

        },
        deleteProject(item) {
            axios.defaults.xsrfHeaderName = "X-CSRFToken";
            axios.defaults.xsrfCookieName = "csrftoken";
            axios.delete(`/deleteProject/${item}/`, {
                    withCredentials: true
                })
                .then(response => {

                    console.log(`Item with ID ${item} deleted successfully!`);
                    this.getProjects()
                    this.fetchLogs();
                })
                .catch(error => {

                    console.log(`Error deleting item with ID ${item}: ${error.message}`);
                });

        },
        leaveProject(item, username) {
            axios.defaults.xsrfHeaderName = "X-CSRFToken";
            axios.defaults.xsrfCookieName = "csrftoken";
            axios.delete(`/leaveProject/${item}/${username}/`, {
                    withCredentials: true
                })
                .then(response => {

                    console.log(`Item with ID ${item} deleted successfully!`);
                    this.getProjects()
                    this.fetchLogs();
                })
                .catch(error => {

                    console.log(`Error deleting item with ID ${item}: ${error.message}`);
                });

        },
        markComplete(item) {
            const formData = new FormData();
            axios.defaults.xsrfHeaderName = "X-CSRFToken";
            axios.defaults.xsrfCookieName = "csrftoken";
            formData.append('username', this.userInfo.username);
            axios.post(`/markComplete/${item}/`, formData, {
                    withCredentials: true
                })
                .then(response => {

                    console.log(`Item with ID ${item} is complete!`);
                    this.getProjects()
                    this.fetchLogs();
                })
                .catch(error => {

                    console.log(`Error deleting item with ID ${item}: ${error.message}`);
                });

        },
        editsubmitForm() {
            const formData = new FormData();
            formData.append('name', this.selectedProject.name);
            formData.append('deadline', this.formatDate(this.selectedProject.deadline));
            formData.append('DAW', this.selectedProject.DAW);

            if (this.image && this.image.length > 0) {
                console.log(this.image[0]);
                formData.append('image', this.image[0]);
            }

            axios
                .post('/editProject/' + this.selectedProject.id + '/', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                })
                .then(response => {
                    console.log(response);
                    this.getProjects();
                    this.fetchLogs();
                })
                .catch(error => {
                    console.log(error);
                    console.log(payload.deadline);
                });

            this.editDialog = false;
        },
        //get colour from album cover
        async getDominantColor() {
            const img = new Image();
            img.crossOrigin = 'Anonymous';
            img.src = `${axios.defaults.baseURL}/${this.selectedProject.coverart}`;
            img.onload = async () => {
                const colorThief = new ColorThief();
                const colorPalette = colorThief.getPalette(img, 10); // change the number to get more or fewer colors
                const rgbStrings = colorPalette.map(color => `rgb(${color[0]}, ${color[1]}, ${color[2]})`);
                this.albumColor = rgbStrings;
                console.log("The colors for the album are:", this.albumColors);
            }
        },
        parseEmails() {
            const emails = this.collabEmail.split(/[,;]/).map(email => email.trim()); // split input by comma or semicolon and trim whitespace
            this.emailList = emails.filter(email => /\S+@\S+\.\S+/.test(email)); // filter out invalid emails
            this.emailList = emailList.split(',');
            this.emailList.map(email => email.trim());
        },
        // used for the project selection to sort between completed and incomplete.
        filterProjects(isCompleted) {
            if (isCompleted) {
                //filters to complete projects inside variable
                this.completedProjects = this.projectInfo.Projects.filter(project => project.completed);
            } else {
                //filters to incomplete projects inside variable
                this.ongoingProjects = this.projectInfo.Projects.filter(project => !project.completed);
            }
        },
        filteredTasks() {
            return this.tasks ?.Projects.filter(task => task.worker === this.userInfo.username);
        },
        async ZipFiles(event) {
            const files = event.target.files;
            const zip = new JSZip();

            // Add files to the zip object
            for (let i = 0; i < files.length; i++) {
                zip.file(files[i].name, files[i]);
            }

            // Generate the zip file and store it in a variable as a Blob
            const content = await zip.generateAsync({
                type: "blob"
            });

            // Convert the Blob to a File object
            this.zippedFile = new File([content], this.selectedTask.song + ".zip", {
                type: "application/zip"
            });

            console.log("Zipped file:", this.zippedFile);
        },
        async fetchMessages() {
            try {
                const response = await axios.get("/messages/" + this.selectedProject.id + "/");
                this.messages = response.data.messages;
            } catch (error) {
                console.error("Error fetching messages:", error);
            }
        },
        async sendMessage() {
            if (this.newMessage.trim() === "") return; // checks to see if the message box is empty
            try {
                const formData = new FormData();
                formData.append('content', this.newMessage); // message from message box
                formData.append('username', this.userInfo.username);
                const response = await axios.post("/messages/" + this.selectedProject.id + "/", formData);
                this.fetchMessages(); //fetch new messages
                this.newMessage = ""; //empty the text box
            } catch (error) {
                console.error("Error sending message:", error);
            }
        },
        async fetchLogs() {
            try {
                const response = await axios.get(`/project-logs/${this.selectedProject.id}/`);
                this.logs = response.data.logs;
            } catch (error) {
                console.error("Error fetching logs:", error);
            }
        },
        submitCollaboratorEmail() {
            if (this.$refs.addCollaboratorForm.validate()) {
                const formData = new FormData();
                formData.append('email', this.collaboratorEmail);
                try {
                    axios.post('/add_user_to_project/'+this.selectedProject.id+'/', formData);
                    this.getProjects();
                    this.addCollaboratorDialog = false;
                    this.collaboratorEmail = "";
            } catch (error) {
                console.error(error);
            }
        }
    },

    }
}
</script>

