<template>
  <v-container fluid class="d-flex align-center justify-center" style="min-height: 100vh;">
    <v-row justify="center">
      <v-col cols="12" sm="8" md="6">
        <v-card>
          <v-card-title>Password Reset</v-card-title>
          <v-card-text>
            <v-form ref="form" v-model="valid" @submit.prevent="sendResetLink">
              <v-text-field
                v-model="email"
                :rules="emailRules"
                label="E-mail"
                required
                outlined
              ></v-text-field>
    
              <v-btn
                :disabled="!valid"
                color="primary"
                class="mr-4"
                type="submit"
              >
                Send Password Reset Link
              </v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data: () => ({
      valid: true,
      email: '',
      emailRules: [
        (v) => !!v || 'E-mail is required',
        (v) => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
    }),
    methods: {
      async sendResetLink() {
        if (this.$refs.form.validate()) {
          try {
            await axios.post('/api/v1/users/reset_password/', {
              email: this.email,
            });
  
            this.$toast.success('Password reset link sent to your email');
          } catch (error) {
            console.error(error);
            this.$toast.error('Error sending password reset link');
          }
        }
      },
    },
  };
  </script>