<template>
<v-app>
    <div class="background"></div>
    <v-main class="d-flex justify-center align-center">
        <v-col cols="10" lg="4" class="mx-auto">
            <v-card class="pa-4" color="grey-darken-4">
                <div class="text-center">
                    <v-avatar size="100" color="grey">
                        <v-icon size="40" color="white">mdi-account</v-icon>
                    </v-avatar>
                    <h1 class="white--text">Login to Soundspot</h1>

                </div>

                <v-form @submit.prevent="loginForm" ref="form">
                    <v-card-text>

                        <v-text-field v-model="username" :rules="usernameRules" color="blue" type="text" label="Username" placeholder="Username" prepend-inner-icon="mdi-account">
                        </v-text-field>

                        <v-text-field v-model="password" :rules="passwordRules" :type="showPassword ? 'text' : 'password'" color="white" label="Password" placeholder="Password" prepend-inner-icon="mdi-key" :append-icon="showPassword ? 'mdi-eye':'mdi-eye-off'" @click:append="showPassword = !showPassword">
                        </v-text-field>

                    </v-card-text>
                    <router-link class="link" to="/sign-up">Need an account? Register</router-link><br>
    <router-link class="link" to="/resetpassword">Forgot your password? Reset here</router-link>

                    <v-card-actions class="justify-center">
                        <v-btn :loading="loadingAnim" type="submit" color="indigo">
                            <span class="white--text px-8">Login</span>
                        </v-btn>
                    </v-card-actions>

                </v-form>
            </v-card>
        </v-col>
    </v-main>
    <v-snackbar color="red" v-model="snackbar">
        Login Failed. Please try again
    </v-snackbar>

</v-app>
</template>

  
<script>
import axios from 'axios'
export default {
    name: 'LoginPage',

    components: {},

    data: () => ({
        snackbar: false,
        showPassword: false,
        valid: false,
        password: '',
        passwordRules: [
            v => !!v || 'Password is required'
        ],
        username: '',
        usernameRules: [
            v => !!v || 'E-mail is required',
        ],
        loadingAnim: false,

        //
    }),
    methods: {
        loginForm() {
            if (this.$refs.form.validate()) {
                this.loadingAnim = true

                const formData = { //login input fields
                    username: this.username,
                    password: this.password
                }

                axios
                    .post('/api/v1/token/login/', formData) //posts to Django for authentication
                    .then(response => {
                        console.log(response)
                        const token = response.data.auth_token
                        this.$store.commit('setToken', token) //adds login token in local storage
                        console.log(this.$store.state.isAuthenticated)
                        axios.defaults.headers.common['Authorization'] = "Token " + token
                        localStorage.setItem('token', token)
                        this.$router.push('/dashboard') // redirect after login successful
                    })
                    .catch(error => {
                        console.log(error)
                    })
                setTimeout(() => {
                    this.loadingAnim = false, //display red error box
                        this.snackbar = true

                }, 3000)
            }
        },
    }
};
</script>
  
<style scoped>
.background {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://images.pexels.com/photos/690779/pexels-photo-690779.jpeg") !important;
    height: 100%;
    width: 100%;
    display: block;
    position: absolute;
    top: 0;
    background-size: cover;
}

.link {
    color: #1976D2;
    /* Replace with your desired primary color */
    text-decoration: none;
}

.link:hover,
.link:active,
.link:focus {
    color: #1976D2;
    /* Replace with your desired primary color */
    text-decoration: none;
}
</style>
