<template>
<v-app id="inspire">

    <v-main class="bg-grey-lighten-3" dark>
        <v-container fluid class="dashboard">

            <v-row>
                <v-col cols="2" class="pt-16">
                    <v-sheet rounded="lg">
                        <v-card-title>
                            <v-subheader align="center">
                                Exports
                            </v-subheader>
                        </v-card-title>
                        <v-list rounded="lg">
                            <v-list-item v-for="n in this.versions.length" :key="n" link @click="loadAndPlayAudio('http://192.168.0.93:8000/' 
                            +this.versions[n - 1].file); this.selectedVersion= this.versions[n-1];">
                                <v-list-item-title>
                                    Version
                                    {{ this.versions[n - 1].id }}
                                </v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-sheet>
                    <v-row justify="center">
                        <v-btn rounded color="white" @click="versionDialog = true" class="mt-7">
                            <v-icon left>mdi-plus</v-icon>
                            Add update
                        </v-btn>
                    </v-row>
                    <v-sheet rounded="lg" class="mt-7">
                        <v-card-title>
                            <v-subheader align="center">
                                Stems
                            </v-subheader>
                        </v-card-title>
                        <v-list rounded="lg">
                            <v-list-item v-for="n in this.stems.length" :key="n" link @click="loadAndPlayAudio('http://192.168.0.93:8000/' +this.stems[n - 1].file)">
                                <v-list-item-title>
                                    {{ this.stems[n - 1].name}}
                                </v-list-item-title>
                            </v-list-item>
                        </v-list>

                    </v-sheet>
                    <v-row justify="center">
                        <v-btn rounded color="white" @click="stemDialog = true" class="mt-7">
                            <v-icon left>mdi-plus</v-icon>
                            Add Stems
                        </v-btn>
                    </v-row>
                    <v-sheet rounded="lg" class="mt-7">
                        <v-card-title>
                            <v-subheader align="center">
                                DAW Files
                            </v-subheader>
                        </v-card-title>
                        <v-list rounded="lg">
                            <v-list-item v-for="n in this.DAWFiles.length" :key="n" link @click="downloadFile('http://192.168.0.93:8000/' + this.DAWFiles[n - 1].file)">
                                <v-list-item-title>
                                    {{ this.DAWFiles[n - 1].name}}
                                </v-list-item-title>
                            </v-list-item>
                        </v-list>

                    </v-sheet>
                    <v-row justify="center">
                        <v-btn rounded color="white" @click="DAWDialog = true" class="mt-7">
                            <v-icon left>mdi-plus</v-icon>
                            Add DAW Files
                        </v-btn>
                    </v-row>
                </v-col>

                <v-col class="pt-16">
                    <v-sheet min-height="70vh" rounded="lg">
                        <v-card :color="albumColor[0]" theme="dark">
                            <div class="project_info" v-if="this.projectPage == true">
                                <div class="d-flex flex-no-wrap justify-space-between">
                                    <div>
                                        <v-card-subtitle class="text-h6 font-weight-light mb-1 pt-3">Song</v-card-subtitle>
                                        <v-card-title class="text-h3 font-weight-bold mb-3 " style="font-family: 'Helvetica Neue', sans-serif; line-height: 1;">
                                            {{ this.songName }}

                                            <v-menu offset-y v-if="$store.state.isAuthenticated" theme="light">
                                                <template v-slot:activator="{ props }">
                                                    <v-btn icon v-bind="props" class="transparent">
                                                        <v-icon>mdi-dots-vertical</v-icon>
                                                    </v-btn>
                                                </template>
                                                <v-card>
                                                    <v-list>
                                                        <v-list-item @click="editDialog = true" v-if="this.userInfo.username === this.selectedProject.creator">
                                                            <v-list-item-title>Edit</v-list-item-title>
                                                        </v-list-item>
                                                        <v-list-item v-if="this.userInfo.username === this.selectedProject.creator && this.selectedProject.completed === false" @click="markComplete(this.selectedProject.name)">
                                                            <v-list-item-title>Mark as complete</v-list-item-title>
                                                        </v-list-item>
                                                        <v-list-item v-if="this.userInfo.username === this.selectedProject.creator && this.selectedProject.completed === true" @click="markComplete(this.selectedProject.name)">
                                                            <v-list-item-title>Mark as incomplete</v-list-item-title>
                                                        </v-list-item>
                                                        <v-list-item v-if="this.userInfo.username === this.selectedProject.creator" @click="deleteProject(this.selectedProject.name)">
                                                            <v-list-item-title>Delete</v-list-item-title>
                                                        </v-list-item>

                                                    </v-list>
                                                </v-card>
                                            </v-menu>
                                        </v-card-title>

                                        <v-card-subtitle class="mb-3" style="text-transform: capitalize;">
                                            Description: {{ this.songData.description}}
                                        </v-card-subtitle>
                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon left>mdi-album</v-icon>
                                            Album: {{ this.songData.project }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon left>mdi-clock</v-icon> Length: {{ this.duration }}
                                            <!-- song length -->
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon color="white">mdi-check</v-icon> Completed: {{ this.songData.completed }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon class="mr-1">mdi-calendar-clock</v-icon>
                                            Created: {{ formatDate(songData.created_at) }}
                                        </v-chip>

                                        <v-chip class="ma-2" :color="albumColor[10]" text-color="white">
                                            <v-icon small class="mr-1">mdi-calendar-edit</v-icon>
                                            Updated: {{ formatDate(songData.updated_at) }}
                                        </v-chip>

                                    </div>
                                    <v-card class="pa-0" elevation="2" style="width: 200px; height: 200px; overflow: hidden; display: flex; justify-content: center; align-items: center;">
                                        <v-img :src="'http://192.168.0.93:8000/' + this.songData.cover" style="object-fit: cover; width: 100%; height: 100%; border-radius: 50%; border: 10px solid white; position: relative;" class="cd-animation">
                                            <div style="position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 40px; height: 40px; border-radius: 50%; background-color: white;"></div>
                                        </v-img>
                                    </v-card>

                                </div>
                            </div>
                        </v-card>
                        <div ref="waveform" class="waveform"></div>
                        <v-card rounded>
                            <v-card-actions>
                                <v-row class="mx-0 justify-content-center">
                                    <v-col cols="12" class="text-center">
                                        <v-btn icon @click="wavesurfer.skipBackward()">
                                            <v-icon>mdi-skip-previous</v-icon>
                                        </v-btn>
                                        <v-btn icon @click="wavesurfer.playPause()">
                                            <v-icon>{{ playing ? 'mdi-pause' : 'mdi-play' }}</v-icon>
                                        </v-btn>
                                        <v-btn icon @click="wavesurfer.skipForward()">
                                            <v-icon>mdi-skip-next</v-icon>
                                        </v-btn>
                                        <v-btn icon @click="downloadAudio()">
                                        <v-icon>mdi-download</v-icon>
                                        </v-btn>
                                    </v-col>
                                </v-row>

                            </v-card-actions>
                        </v-card>
                        <v-col>
                            <v-card>
                                <v-tabs v-model="tab" :color=this.albumColor[2] class="mt-3 centered-tab-buttons" grow>
                                    <v-tab value="one">Tasks</v-tab>
                                    <v-tab value="two">Messages</v-tab>
                                </v-tabs>
                                <v-card-text>
                                    <v-window v-model="tab">
                                        <v-window-item value="one">
                                            <div style="display: flex; flex-wrap: wrap; justify-content: space-around;">
            <v-card v-for="(task, index) in this?.tasks" :key="index" width="341" :title="task.title" :subtitle="task.song" :text="task.description">

                <v-card-actions>

                    <v-btn rounded v-if="task.completed===false" :color="white" @click="finishTaskDialog = true; selectedTask=task">
                        <v-icon left>mdi-check</v-icon>
                        Mark as complete
                    </v-btn>
                    <v-btn rounded :color="white" v-if="task.completed===true">
                        <v-icon left>mdi-check</v-icon>
                        Completed
                    </v-btn>
                </v-card-actions>
            </v-card>
        </div>
                                            <v-dialog v-model="finishTaskDialog" max-width="600px">
                                                <v-card>
                                                    <v-card-title>
                                                        <div style="text-align: center;">
                                                            <span class="headline">Finish Task</span>
                                                        </div>
                                                    </v-card-title>

                                                    <v-card-text>
                                                        <v-container>
                                                            <v-row>
                                                                <v-col cols="12">
                                                                    <v-file-input v-if="this.typeSelect!=='DAW Files'" label="Upload file(s)" @change="onFileSelected" multiple></v-file-input>
                                                                    <v-file-input v-if="this.typeSelect==='DAW Files'" label="Upload DAW files" @change="ZipFiles" multiple></v-file-input>

                                                                </v-col>
                                                                <v-col cols="12">
                                                                    <v-textarea v-model="taskDescription" :counter="100" label="Description" required></v-textarea>
                                                                </v-col>
                                                                <v-col cols="12">
                                                                    <v-select v-model="typeSelect" @click="console.log(this.typeSelect)" :items="this.finishSelect" :rules="[v => !!v || 'Item is required']" label="File type:" required></v-select>
                                                                </v-col>

                                                            </v-row>
                                                        </v-container>
                                                    </v-card-text>

                                                    <v-card-actions>
                                                        <v-spacer></v-spacer>
                                                        <v-btn color="blue darken-1" text @click="finishTaskDialog = false">Cancel</v-btn>
                                                        <v-btn color="blue darken-1" text @click="finishTaskSubmitForm()">Submit</v-btn>
                                                    </v-card-actions>
                                                </v-card>
                                            </v-dialog>

                                            <div style="display: flex; justify-content: center; padding-bottom: 1px; padding-top: 20px;">
                                            </div>
                                        </v-window-item>
                                        <v-window-item value="two">
                                            <div v-for="message in messages" :key="message.timestamp">
                                                <span class="mdi mdi-account-circle profile-icon"></span>
                                                <span class="username">{{ message.user }}:</span>
                                                <span class="message">{{ message.content }}</span>
                                            </div>
                                            <div class="message-input">
                                                <input type="text" v-model="newMessage" placeholder="Type a message..." />
                                                <button @click="sendMessage">Send</button>
                                            </div>
                                        </v-window-item>

                                    </v-window>
                                </v-card-text>
                            </v-card>
                        </v-col>

                        <div class="d-flex align-center flex-column">
                            <v-col cols="12" sm="6" md="4" v-if="this.projectPage == true">
                                <div>

                                    <v-dialog v-model="versionDialog" max-width="600px">
                                        <v-card>
                                            <v-card-title>
                                                <div style="text-align: center;">
                                                    <span class="headline">Add Update</span>
                                                </div>
                                            </v-card-title>

                                            <v-card-text>
                                                <v-container>
                                                    <v-row>
                                                        <v-col cols="12">
                                                            <v-file-input label="Upload audio file" @change="onFileSelected" accept="audio/*"></v-file-input>
                                                        </v-col>

                                                        <v-col cols="12">
                                                            <v-textarea label="Description" v-model="song.description"></v-textarea>
                                                        </v-col>
                                                    </v-row>
                                                </v-container>
                                            </v-card-text>

                                            <v-card-actions>
                                                <v-spacer></v-spacer>
                                                <v-btn color="blue darken-1" text @click="versionDialog = false">
                                                    Cancel
                                                </v-btn>
                                                <v-btn color="blue darken-1" text @click="submitUpdateForm">
                                                    Submit
                                                </v-btn>
                                            </v-card-actions>
                                        </v-card>
                                    </v-dialog>
                                    <v-dialog v-model="DAWDialog" max-width="600px">
                                        <v-card>
                                            <v-card-title>
                                                <div style="text-align: center;">
                                                    <span class="headline">Add DAW Files</span>
                                                </div>
                                            </v-card-title>

                                            <v-card-text>
                                                <v-container>
                                                    <v-row>
                                                        <v-col cols="12">
                                                            <v-file-input label="Upload DAW files" @change="ZipFiles" multiple></v-file-input>
                                                        </v-col>

                                                        <v-col cols="12">
                                                            <v-textarea label="Description" v-model="DAWDescription"></v-textarea>
                                                        </v-col>
                                                    </v-row>
                                                </v-container>
                                            </v-card-text>

                                            <v-card-actions>
                                                <v-spacer></v-spacer>
                                                <v-btn color="blue darken-1" text @click="DAWDialog = false">
                                                    Cancel
                                                </v-btn>
                                                <v-btn color="blue darken-1" text @click="DAWSubmitForm">
                                                    Submit
                                                </v-btn>
                                            </v-card-actions>
                                        </v-card>
                                    </v-dialog>
                                </div>
                            </v-col>
                        </div>
                        <div v-if="this.projectPage == true">
                            <!-- this is a centred table that will contain information about songs in a project -->
                            <v-container class="mt-5">
                            </v-container>

                        </div>

                        <!-- This dialogue is for adding tasks -->

                        <div>
                            <v-dialog v-model="stemDialog" max-width="600px">
                                <v-card>
                                    <v-card-title>
                                        <div style="text-align: center;">
                                            <span class="headline">Add Stems</span>
                                        </div>
                                    </v-card-title>

                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12">
                                                    <v-text-field v-model="stemTitle" :counter="50" label="Title" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-file-input label="Upload audio file" @change="onFileSelected" accept="audio/*"></v-file-input>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                    </v-card-text>

                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="stemDialog = false">Cancel</v-btn>
                                        <v-btn color="blue darken-1" text @click="stemSubmitForm()">Submit</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>

                        </div>

                    </v-sheet>
                </v-col>
            </v-row>
        </v-container>
    </v-main>
</v-app>
</template>

<script>
import {
    ref
} from 'vue';
import WaveSurfer from "wavesurfer.js";
import ColorThief from 'colorthief';
import JSZip from "jszip";

import axios from 'axios'
export default {
    props: {
        projectName: {
            type: String,
            required: true
        },
        songName: {
            type: String,
            required: true
        }
    },

    beforeCreate() {
        this.projectPage = true

        this.$store.commit('initializeStore')
        const token = this.$store.state.token
        console.log(this.$store.state.isAuthenticated)

        if (token) {
            axios.defaults.headers.common['Authorization'] = "Token " + token
        } else {
            axios.defaults.headers.common['Authorization'] = ''
            this.$router.push('/login');

        }
        axios
            .get('/api/v1/users/me/', token)
            .then(response => {
                this.userInfo = response.data
                console.log(this.userInfo)
                this.getSongData();
                this.getVersions();
                this.getStems();
                this.getDAW();
                this.getTasks();
                this.fetchMessages();

            })
            .catch(error => {
                console.log(error)
            })

    },
    data: () => ({

        userInfo: '',
        projectInfo: '',
        selectedProjectSongs: '',
        projectLength: 0,

        dialog: false,
        projectDialog: false,
        editDialog: false,
        song: {
            title: '',
            description: '',
            audioFile: null,
        },

        selectedDate: null,
        addPage: false,
        projectPage: false,
        links: [
            'My Projects',
            'Messages',
            'Profile',
            'Updates',
        ],

        select: '',
        collabEmail: '',
        emailList: [],
        items: [
            'FL Studio',
            'Ableton Live',
            'Logic Pro',
            'Reason',
            'Avid Pro Tools'
        ],
        image: null,
        imagePath: '',
        confirmDelete: false,
        albumColor: '',
        completedProjects: [],
        ongoingProjects: [],

        confirmDelete: false,
        infoDialog: false,
        selectedItem: {},
        tab: null,

        // This is data for stems
        stemTitle: '',
        stems: [],
        stemDialog: false,

        //new song data variables for song page,
        DAWFiles: [],
        DAWDialog: false,
        songData: [],
        versions: [],
        versionDialog: false,
        selectedVersion: '',
        duration: null,
        //music player
        wavesurfer: null,
        versionDialog: false,
        playing: false,
        volume: 50,
        song: {
            description: "",
        },
        projectPage: true,
        //DAW Files
        zippedFile: null,
        DAWDescription: '',

        //tasks
        finishTaskDialog: false,
        taskDialog: false,
        taskTitle: '',
        taskDescription: '',
        songNames: [],
        tasks: null,
        typeSelect: '',
        selectedTask: null,
        zippedFile: null,
        finishSelect: [
            'DAW Files',
            'Music File',
            'Stems'
        ],

        //messages
        messages: [],
        newMessage: "",

    }),
    //code executed before website is fully loaded
    mounted() {
        //create music player
        this.wavesurfer = WaveSurfer.create({
            //create waveform for audio when selected.
            container: this.$refs.waveform,
            waveColor: "black",
            progressColor: "#54524c",
        });
        //playback controls

        this.wavesurfer.on("play", () => {
            this.playing = true;
        });

        this.wavesurfer.on("pause", () => {
            this.playing = false;
        });

        this.wavesurfer.on("finish", () => {
            this.playing = false;
        });

        this.wavesurfer.setVolume(this.volume / 100);
    },

    methods: {
        onRowClick(item) {
            this.selectedItem = item;
            this.infoDialog = true;
        },
        //formats the date into a readable format 
        formatDate(dateString) {
            const date = new Date(dateString);
            const year = date.getFullYear();
            const month = (date.getMonth() + 1).toString().padStart(2, '0');
            const day = date.getDate().toString().padStart(2, '0');
            const hours = date.getHours().toString().padStart(2, '0');
            const minutes = date.getMinutes().toString().padStart(2, '0');

            return `${day}-${month}-${year} ${hours}:${minutes}`;
        },
        getVersions() {
            console.log(this.projectName)
            axios
                .get('/getVersions/' + this.songName + "/" + this.projectName + "/")
                .then(response => {

                    this.versions = response.data.Versions
                    console.log(this.versions)
                    // this.projectLength = this.projectInfo.Projects.length
                    // if (this.projectInfo && this.projectInfo.Projects && this.projectInfo.Projects.length > 0) {
                    //     this.projectPage = true

                    //     this.selectedProject = this.projectInfo.Projects[0];
                    // }
                    this.projectPage = true
                    // this.filterProjects(true) // filters projects from complete to incomplete
                    // this.filterProjects(false)

                })
                .catch(error => {
                    console.log(error)

                })
        },
        getStems() {
            console.log(this.projectName)
            axios
                .get('/getStems/' + this.songName + "/" + this.projectName + "/")
                .then(response => {

                    this.stems = response.data.Stems
                    console.log(this.versions)
                    // this.projectLength = this.projectInfo.Projects.length
                    // if (this.projectInfo && this.projectInfo.Projects && this.projectInfo.Projects.length > 0) {
                    //     this.projectPage = true

                    //     this.selectedProject = this.projectInfo.Projects[0];
                    // }
                    this.projectPage = true
                    // this.filterProjects(true) // filters projects from complete to incomplete
                    // this.filterProjects(false)

                })
                .catch(error => {
                    console.log(error)

                })
        },
        getSongData() {
            axios
                .get('/getSongData/' + this.songName + "/" + this.projectName + "/")
                .then(response => {
                    console.log(this.albumColor)

                    this.songData = response.data.SongData[0]
                    this.getDominantColor()
                    console.log(this.songData)

                })
                .catch(error => {
                    console.log(error)

                })
        },
        //generates a url for the uploaded picture to display
        onFileInput(event) {
            const data = URL.createObjectURL(event.target.files[0]);
            console.log(this.image[0])
            this.imagePath = data;
        },
        loadAndPlayAudio(audioUrl) {
            console.log(audioUrl) //debug
            //checks if wavesurfer instance exists and if audioUrl is not empty
            if (this.wavesurfer && audioUrl) {
                //loads the audio file from the URL into the player
                this.wavesurfer.load(audioUrl);
                this.wavesurfer.on('ready', () => {
                    // get the duration of the loaded audio
                    this.duration = this.wavesurfer.getDuration();
                    //converts seconds to minutes and rounds down
                    const minutes = Math.floor(this.duration / 60);
                    const seconds = Math.round(this.duration % 60);
                    //format to human readable format. Pad the remaining digits with 0's if needed.
                    this.duration = `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
                    this.wavesurfer.play();
                });
            }

        },

        onFileSelected(event) {
            this.song.audioFile = event.target.files[0];
        },
        // Sends the song data to django via POST 
        submitUpdateForm() {
            console.log(this.song);
            // Submit the form and handle the response here
            const formData = new FormData();
            formData.append('file', this.song.audioFile)
            formData.append('description', this.song.description)
            formData.append('username', this.userInfo.username)

            console.log(formData)
            axios
                .post('/getVersions/' + this.songName + "/" + this.projectName + "/", formData)
                .then(response => {
                    console.log(response)
                    this.getVersions()

                })
                .catch(error => {
                    console.log(error)

                })
            this.getVersions();
            this.versionDialog = false;
        },
        stemSubmitForm() {
            console.log(this.song);
            // Submit the form and handle the response here
            const formData = new FormData();
            formData.append('file', this.song.audioFile)
            formData.append('description', this.stemTitle)
            formData.append('username', this.userInfo.username)

            console.log(formData)
            axios
                .post('/getStems/' + this.songName + "/" + this.projectName + "/", formData)
                .then(response => {
                    console.log(response)
                    this.getStems();

                })
                .catch(error => {
                    console.log(error)

                })
            this.stemDialog = false;
        },
        DAWSubmitForm() {
            console.log(this.song);
            // Submit the form and handle the response here
            const formData = new FormData();
            formData.append('file', this.zippedFile)
            formData.append('description', this.DAWDescription)
            formData.append('username', this.userInfo.username)

            console.log(formData)
            axios
                .post('/getDAWFiles/' + this.songName + "/" + this.projectName + "/", formData)
                .then(response => {
                    console.log(response)
                    this.getDAW();

                })
                .catch(error => {
                    console.log(error)

                })
            this.DAWDialog = false;
        },
        getDAW() {
            console.log(this.projectName)
            axios
                .get('/getDAWFiles/' + this.songName + "/" + this.projectName + "/")
                .then(response => {

                    this.DAWFiles = response.data.DAW_File
                    console.log("This is the DAW files" + this.DAWFiles)
                    // this.projectLength = this.projectInfo.Projects.length
                    // if (this.projectInfo && this.projectInfo.Projects && this.projectInfo.Projects.length > 0) {
                    //     this.projectPage = true

                    //     this.selectedProject = this.projectInfo.Projects[0];
                    // }
                    this.projectPage = true
                    // this.filterProjects(true) // filters projects from complete to incomplete
                    // this.filterProjects(false)

                })
                .catch(error => {
                    console.log(error)

                })
        },
        //deletes song entries in project
        deleteItem(item) {
            axios.defaults.xsrfHeaderName = "X-CSRFToken";
            axios.defaults.xsrfCookieName = "csrftoken";
            axios.delete(`/deleteSong/${item}/`, {
                    withCredentials: true
                })
                .then(response => {

                    console.log(`Item with ID ${item} deleted successfully!`);
                    this.getProjectSongs()
                })
                .catch(error => {

                    console.log(`Error deleting item with ID ${item}: ${error.message}`);
                });

        },
        markComplete(item) {
            axios.defaults.xsrfHeaderName = "X-CSRFToken";
            axios.defaults.xsrfCookieName = "csrftoken";
            axios.post(`/markComplete/${item}/`, {
                    withCredentials: true
                })
                .then(response => {

                    console.log(`Item with ID ${item} is complete!`);
                    this.getProjects()
                })
                .catch(error => {

                    console.log(`Error deleting item with ID ${item}: ${error.message}`);
                });

        },

        //get colour from album cover
        async getDominantColor() {
            const img = new Image();
            img.crossOrigin = 'Anonymous';
            img.src = `http://192.168.0.93:8000/${this.songData.cover}`;
            img.onload = async () => {
                const colorThief = new ColorThief();
                const colorPalette = colorThief.getPalette(img, 10); // change the number to get more or fewer colors
                const rgbStrings = colorPalette.map(color => `rgb(${color[0]}, ${color[1]}, ${color[2]})`);
                this.albumColor = rgbStrings;
                console.log("The colors for the album are:", this.albumColors);
            }
        },
        // used for the project selection to sort between completed and incomplete.
        async ZipFiles(event) {
            const files = event.target.files;
            const zip = new JSZip();

            // Add files to the zip object
            for (let i = 0; i < files.length; i++) {
                zip.file(files[i].name, files[i]);
            }

            // Generate the zip file and store it in a variable as a Blob
            const content = await zip.generateAsync({
                type: "blob"
            });

            // Convert the Blob to a File object
            this.zippedFile = new File([content], this.songName + ".zip", {
                type: "application/zip"
            });

            console.log("Zipped file:", this.zippedFile);
        },
        downloadFile(url) {
            let link = document.createElement('a'); //new anchor for hyperlink
            link.href = url; //href hyperlink url to redirect to
            link.download = url.split('/').pop(); // provides the default filename for the download
            document.body.appendChild(link);
            link.click(); //execute the download
            document.body.removeChild(link);
        },
    async sendMessage() {
        if (this.newMessage.trim() === "") return;
        try {
            const formData = new FormData();
            formData.append('content', this.newMessage);
            formData.append('username', this.userInfo.username);
            const response = await axios.post("/messages/" + this.songData.projectId + "/", formData);
            this.fetchMessages();
            this.newMessage = "";
        } catch (error) {
            console.error("Error sending message:", error);
        }
    },
    async fetchMessages() {
            try {
                const response = await axios.get("/messages/" + this.songData.projectId + "/");
                this.messages = response.data.messages;
            } catch (error) {
                console.error("Error fetching messages:", error);
            }
        },
    filteredTasks() {
        return this.tasks?.Projects.filter(task => task.worker === this.userInfo.username && task.song === this.songName);
    },
    getTasks() {
        axios
            .get('/getTasks/' + this.projectName + "/")
            .then(response => {
                this.tasks = response.data
                this.tasks = this.filteredTasks();
                console.log(this.tasks)
            })
            .catch(error => {
                console.log(error)

            })

    },
    finishTaskSubmitForm() {
        const formData = new FormData();
        formData.append('description', this.taskDescription)

        if (this.typeSelect === 'Music File') {
            formData.append('file', this.song.audioFile);
            formData.append('username', this.userInfo.username);

            axios
                .post('/getVersions/' + this.selectedTask.song + '/' + this.projectName + '/', formData)
                .then(response => {
                    console.log(response);
                    this.getVersions();
                })
                .catch(error => {
                    console.log(error);
                });
        }
        if (this.typeSelect === 'Stems') {
            formData.append('file', this.song.audioFile);
            formData.append('username', this.userInfo.username);

            axios
                .post('/getStems/' + this.selectedTask.song + '/' + this.projectName + '/', formData)
                .then(response => {
                    console.log(response);
                    this.getStems();
                })
                .catch(error => {
                    console.log(error);
                });
        }
        if (this.typeSelect === 'DAW Files') {
            formData.append('file', this.zippedFile);
            formData.append('username', this.userInfo.username);

            axios
                .post('/getDAWFiles/' + this.selectedTask.song + '/' + this.projectName + '/', formData)
                .then(response => {
                    console.log(response);
                    this.getDAW();
                })
                .catch(error => {
                    console.log(error);
                });
        }
        axios
            .post('/finishTask/' + this.selectedTask.id + "/")
            .then(response => {
                console.log(response)
                this.getTasks()
                this.fetchLogs();

            })
            .catch(error => {
                console.log(error)

            })
        this.finishTaskDialog = false;
    },
    downloadAudio() {
        const link = document.createElement('a');
        link.href = 'http://192.168.0.93:8000/'+this.selectedVersion.file+'/' //set the hyperlink
        link.download = this.songName+'.wav';  //set the audio file name
        link.click(); //simulate a click on the href
    },
    },
    

}
</script>

